<template>
  <section class="slide55" data-background-image="/static/img/55.jpg">
    <div class="sl-block text-wrapper">
      <h6 class="fragment fade-up" data-autoslide="2000"> </h6>
      <h6 class="fragment fade-up" data-autoslide="2000"></h6>
      <p></p>
      <h6 class="fragment fade-up" data-autoslide="2000"></h6>
      <h6 class="fragment fade-up" data-autoslide="2000"></h6>
      <h6 class="fragment fade-up" data-autoslide="2000"></h6>
      <p></p>
      <h6 class="fragment fade-up" data-autoslide="2000"></h6>
      <h6 class="fragment fade-up" data-autoslide="2000"></h6>
      <p></p>
      <h6 class="fragment fade-up" data-autoslide="3000"></h6>
      <h6 class="fragment fade-up" data-autoslide="3000"></h6>
      <h5 class="fragment fade-up" data-autoslide="3000"></h5>
      <h5 class="fragment fade-up" data-autoslide="3000">{{loveTimes}}</h5>
    </div>
  </section>
</template>

<script>
 export default {
    data() {
      return {
        timer: null,
        loveTimes: 0
      };
    },
    methods: {
      setLoveTimes() {
        let start=new Date("2020/10/17 00:00:00").getTime()/1000;
        let now=Math.floor(new Date().getTime()/1000);
        let loveTimes=now-start;
        this.loveTimes=loveTimes
      }
    },
    mounted() {
      this.timer = setInterval(this.setLoveTimes, 1000);
    },
    beforeDestroy() {
      clearInterval(this.timer);
    }
  };
</script>

